import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { BsButton } from '../../atoms/button/button';
import { ButtonVariant } from '../../atoms/button/button.types';
import { TextSize } from '../../atoms/text/text.types';
import { TitleSize, TitleTag, TitleVariant } from '../../atoms/title/title.types';
import { Breakpoint, compareBreakpoint } from '../../../utils/breakpoints';

/**
 * Newsletter banner component.
 */
@Component({
  tag: 'bs-newsletter-banner',
  styleUrl: 'newsletter-banner.scss',
  shadow: true,
})
export class BsNewsletterBanner {
  /**
   * Section's title
   */
  @Prop() sectionTitle: string;

  /**
   * Subtitle
   */
  @Prop() subtitle: string;

  /**
   * Description.
   */
  @Prop() button: BsButton | string;

  @Memoize('button')
  get parsedButton(): BsButton {
    return parseIfString(this.button);
  }

  render() {
    return (
      <Host>
        <div class="newsletter-banner">
          <div class="text-wrapper">
            <bs-title
              tag={TitleTag.H2}
              text={this.sectionTitle}
              size={TitleSize.Head2}
              variant={TitleVariant.Slash}
            />
            <bs-text text={this.subtitle} size={TextSize.Small} />
          </div>

          <bs-button
            {...this.parsedButton}
            isFullWidth={compareBreakpoint('max', Breakpoint.md)}
            variant={ButtonVariant.Primary}
          />
        </div>
      </Host>
    );
  }
}
